.po-problem-details .row{margin-left:0;margin-right:0}.po-problem-details .problem-details-content{overflow-y:scroll;overflow-x:hidden}.po-problem-details .problem-details-content .problem-details-header{margin-top:30px;margin-bottom:30px;border-left-width:3px;border-left-color:green;border-left-style:solid;padding-left:20px}.po-problem-details .problem-details-content h3{font-family:Consolas,Courier New}.po-problem-details .problem-details-content p{font-size:16px;font-family:Consolas,Courier New;font-weight:400}.po-problem-details .problem-details-content pre{padding:0 0 0 10px;border-left-width:none;background-color:white;border-radius:0;border:0 none white;border-left-width:3px;border-left-style:solid;border-left-color:#E75440}.po-problem-details .problem-details-content pre p{font-family:Consolas,Courier New;font-size:16px;background-color:white;margin-bottom:0}.po-problem-details .problem-details-submit{padding-left:0;padding-right:0}.po-problem-details .problem-details-submit #code-editor{visibility:hidden}.po-problem-details .problem-details-controller{background-color:rgba(93,164,195,0.06);padding-top:3px;padding-bottom:3px;border-top-color:#e7e7e7;border-top-width:1px;border-top-style:solid}.po-problem-details .problem-details-controller button{font-family:Consolas,Courier New;margin-left:15px;float:right;border-radius:2px;line-height:12px}.po-problem-details .problem-details-controller .problem-details-controller-select{margin-left:15px;float:right;font-family:Consolas,Courier New}.po-problem-details .problem-details-controller .problem-details-controller-select ul{font-family:Consolas,Courier New}


