.po-status-list ul{padding-left:0;padding-right:0}.po-status-list li{list-style-type:none;font-size:18px;padding-top:5px;padding-bottom:5px}.po-status-list li a{line-height:22px}.po-status-list li .border-red{border-left-width:3px;border-left-color:#E75440;border-left-style:solid}.po-status-list li .border-green{border-left-width:3px;border-left-color:green;border-left-style:solid}.po-status-list li .border-other{border-left-width:3px;border-left-color:#289ad8;border-left-style:solid}.po-status-list li .sub-title{font-size:12px;line-height:1px;color:#777}.po-status-list li .content-red{color:red}.po-status-list li .content-green{color:green}.po-status-list li .content-other{color:#289ad8}.po-status-list li .status-statistic{padding-top:3px;font-size:16px;text-align:center;color:#337ab7}.po-status-list li .running-icon{width:20px;height:20px;box-sizing:border-box;margin-top:15px;border:solid 2px transparent;border-top-color:#29d;border-left-color:#29d;border-radius:50%;-webkit-animation:nprogress-spinner 400ms linear infinite;animation:nprogress-spinner 400ms linear infinite}


